<template><section>

<h1>dropdown 案例</h1>
<hr>
<h3>dropdown 基础用法</h3>
<div class="demo-block">
    <in-dropdown @command="command">
        <span class="account"><i class="fa fa-user-circle-o"></i> zhushunsheng</span>
        <in-dropdown-menu slot="dropdown">
            <in-dropdown-item command="account">账户设置</in-dropdown-item>
            <in-dropdown-item command="update">修改密码</in-dropdown-item>
            <in-dropdown-item command="login">安全退出</in-dropdown-item>
        </in-dropdown-menu>
    </in-dropdown>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-dropdown @command="command">
    <span class="account"><i class="fa fa-user-circle-o"></i> zhushunsheng</span>
    <in-dropdown-menu slot="dropdown">
        <in-dropdown-item command="account">账户设置</in-dropdown-item>
        <in-dropdown-item command="update">修改密码</in-dropdown-item>
        <in-dropdown-item command="login">安全退出</in-dropdown-item>
    </in-dropdown-menu>
</in-dropdown>


</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">in-dropdown</span> <span class="hljs-attr">@command</span>=<span class="hljs-string">"command"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"account"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"fa fa-user-circle-o"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span> zhushunsheng<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">in-dropdown-menu</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"dropdown"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">in-dropdown-item</span> <span class="hljs-attr">command</span>=<span class="hljs-string">"account"</span>&gt;</span>账户设置<span class="hljs-tag">&lt;/<span class="hljs-name">in-dropdown-item</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">in-dropdown-item</span> <span class="hljs-attr">command</span>=<span class="hljs-string">"update"</span>&gt;</span>修改密码<span class="hljs-tag">&lt;/<span class="hljs-name">in-dropdown-item</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">in-dropdown-item</span> <span class="hljs-attr">command</span>=<span class="hljs-string">"login"</span>&gt;</span>安全退出<span class="hljs-tag">&lt;/<span class="hljs-name">in-dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">in-dropdown-menu</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">in-dropdown</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>:{
        command(command){
            <span class="hljs-keyword">this</span>.$message(command);
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>in-dropdown-menu组件属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>slot</td>
<td>组件下拉选</td>
<td>string</td>
<td>dropdown</td>
<td>dropdown</td>
</tr>
</tbody>
</table>
<h3>in-dropdown-item组件属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>command</td>
<td>下拉选中的值</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>dropdown组件事件</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>参数回调</th>
</tr>
</thead>
<tbody>
<tr>
<td>command</td>
<td>组件选中下拉选的时候触发</td>
<td>command</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.account{
    cursor: pointer;
}
</style>
<script>
export default {
    methods:{
        command(command){
            this.$message(command);
        }
    }
}
</script>


