<template><section>
<h1>datepicker 案例</h1>
<hr>
<h3>基础用法</h3>
<div class="demo-block"> 
    <in-datepicker v-model="date1"></in-datepicker>
    <in-datepicker disabled v-model="date2"></in-datepicker>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-datepicker v-model="date1"></in-datepicker>
<in-datepicker disabled v-model="date2"></in-datepicker>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">in-datepicker</span>  <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-datepicker</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">in-datepicker</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-datepicker</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>in-datepicker 组件属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>booblean</td>
<td>true/false</td>
<td>false</td>
</tr>
<tr>
<td>value(v-model)</td>
<td>选框的值</td>
<td>booblean</td>
<td>false</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>in-datepicker 组件事件</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>参数回调</th>
</tr>
</thead>
<tbody>
<tr>
<td>focus</td>
<td>组件获取焦点时触发</td>
<td>event</td>
</tr>
<tr>
<td>blur</td>
<td>组件失去焦点时触发</td>
<td>event</td>
</tr>
<tr>
<td>change</td>
<td>组件值改变时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
            date1:'',
            date2:'2018-08-01'
        }
    }
}
</script>


