<template><section>
<h1>checkbox 案例</h1>
<hr>
<h3>基础用法</h3>
<div class="demo-block">
    <in-checkbox v-model="value1"></in-checkbox> {{value1}}
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-checkbox v-model="value1"></in-checkbox> {{value1}}



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">in-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-checkbox</span>&gt;</span> {{value1}}

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data(){
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">value1</span>:<span class="hljs-literal">true</span>
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>多选框组</h3>
<div class="demo-block">
<in-checkbox-group v-model="value2">
    <in-checkbox :label="1">微博</in-checkbox>
    <in-checkbox :label="2">小红书</in-checkbox>
    <in-checkbox :label="3">B站</in-checkbox>
</in-checkbox-group>
<p>{{value2}}</p>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-checkbox-group v-model="value2">
    <in-checkbox :label="1">微博</in-checkbox>
    <in-checkbox :label="2">小红书</in-checkbox>
    <in-checkbox :label="3">B站</in-checkbox>
</in-checkbox-group>


</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">in-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">in-checkbox</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"1"</span>&gt;</span>微博<span class="hljs-tag">&lt;/<span class="hljs-name">in-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">in-checkbox</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"2"</span>&gt;</span>小红书<span class="hljs-tag">&lt;/<span class="hljs-name">in-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">in-checkbox</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"3"</span>&gt;</span>B站<span class="hljs-tag">&lt;/<span class="hljs-name">in-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">in-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data(){
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">value2</span>:<span class="hljs-literal">true</span>
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>in-checkbox 组件属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>booblean</td>
<td>true/false</td>
<td>false</td>
</tr>
<tr>
<td>value(v-model)</td>
<td>选框的值</td>
<td>booblean</td>
<td>false</td>
<td>false</td>
</tr>
<tr>
<td>label</td>
<td>在in-checkbox-group 选框的值</td>
<td>string/number</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>in-checkbox-group 组件属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value(v-model)</td>
<td>选框的值</td>
<td>array</td>
<td>[]</td>
<td>[]</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
            value1:true,
            value2:[]
        }
    }
}
</script>


