<template><section>
<h1>message 案例</h1>
<hr>
<h3>message用法</h3>
<div class="demo-block">
    <in-button @click="messageClick">显示message</in-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-button @click="messageClick">显示message</in-button>


</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">in-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"messageClick"</span>&gt;</span>显示message<span class="hljs-tag">&lt;/<span class="hljs-name">in-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>:{
        messageClick(){
            <span class="hljs-keyword">this</span>.$message(<span class="hljs-string">"这是一条消息提示"</span>);
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>confirm用法</h3>
<div class="demo-block">
    <in-button @click="confirmClick">显示confirm</in-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-button @click="confirmClick">显示message</in-button>


</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">in-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"confirmClick"</span>&gt;</span>显示message<span class="hljs-tag">&lt;/<span class="hljs-name">in-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>:{
        confirmClick(){
            <span class="hljs-keyword">this</span>.$confirm({
                <span class="hljs-attr">title</span>:<span class="hljs-string">'消息标题'</span>,<span class="hljs-comment">//消息标题</span>
                text:<span class="hljs-string">'消息内容'</span>,<span class="hljs-comment">//消息内容</span>
                confirmButton:<span class="hljs-string">'确认'</span>,<span class="hljs-comment">//确认按钮文字</span>
                cancelButton:<span class="hljs-string">'取消'</span>,<span class="hljs-comment">//取消按钮文字</span>
                action:<span class="hljs-function">(<span class="hljs-params">action</span>)=&gt;</span>{
                    <span class="hljs-built_in">console</span>.log(action);
                }
            });
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>confirm组件参数</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>提示标题</td>
<td>string</td>
<td>-</td>
<td>标题</td>
</tr>
<tr>
<td>text</td>
<td>提示文字内容</td>
<td>string</td>
<td>-</td>
<td>消息内容</td>
</tr>
<tr>
<td>confirmButton</td>
<td>确认按钮文字</td>
<td>string</td>
<td>-</td>
<td>确认</td>
</tr>
<tr>
<td>cancelButton</td>
<td>取消按钮文字</td>
<td>string</td>
<td>-</td>
<td>取消</td>
</tr>
<tr>
<td>action</td>
<td>点击按钮回调 返回confirm/cancel</td>
<td>function</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    methods:{
        messageClick(){
            this.$message("这是一条消息提示");
        },
        confirmClick(){
            this.$confirm({
                title:'消息标题',//消息标题
                text:'消息内容',//消息内容
                confirmButton:'确认',//确认按钮文字
                cancelButton:'取消',//取消按钮文字
                action:(action)=>{
                    console.log(action);
                }
            });
        }

    }
}
</script>


